<template>
  <div class="listmenu">
    <!-- <el-button type="primary" @click="openFullScreen2"> 服务方式 </el-button> -->
    <el-table
      :data="SeckillGetter"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >


      <el-table-column
        prop="title"
        label="秒杀列表"
        sortable
      ></el-table-column>

      <el-table-column prop="address" label="操作">
        <template slot-scope="scope">
          <div>
            <!-- 这里自定义 表格 在template中 添加 slot-scope = scope -->
            <!-- 判断状态 如果说 后台返回的 status 是 1 就是启用 否则就禁用  -->
            <el-button type="primary" v-if="scope.row.status == 1" plain
              >启用</el-button
            >
            <el-button type="info" v-else plain>禁用</el-button>

            <el-button type="success" @click="edit(scope.row.id)" plain
              >编辑</el-button
            >

            <delbutton @delbtn="del(scope.row.id)"></delbutton>

          </div>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
// 引入仓库
import { mapGetters, mapActions } from "vuex";
import { reqseckillDel } from "../../../util/request";
import { successAlert, warningAlert } from "../../../util/alert";

export default {
  computed: {
    ...mapGetters({
     SeckillGetter:"seckill/SeckillGetter"
    }),
  },

  methods: {
    ...mapActions({
      actionSeckillList:"seckill/actionSeckillList"
    }),

    // 删除函数
    del(id) {
      // 删除请求
      reqseckillDel(id).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionSeckillList()
        } else {
          warningAlert(res.data.msg);
        }
      });
    },

    // 编辑函数
    edit(id) {
      // 给父组建进行传参数
      this.$emit("listedit", id);
    },

  },

  data() {
    return {
      flag: true,
    };
  },

  // 挂载完成之后 进行 调用 发送请求函数
  mounted() {
    this.actionSeckillList()
  },
};
</script>

<style lang="">
.cell {
  text-align: center;
}

.el-table,
.submit {
  width: 100%;
  margin-top: 100px;
}
.submit {
  font-size: 16px;
}

#img {
  width: 100px;
  height: 100px;
  border-radius: 6px;
}
</style>


