<template>
  <div>
    <!--  控制显示跟隐藏 -->
    <!-- 
        说明::visible.sync="dialogFormVisible" 控制弹窗显示跟隐藏 
             opened:当动画结束之后的回调函数 主要用来 控制 副文本的创建
             close:点击X号之后触发的函数
     -->

    <el-dialog
      :title="isShow.edit ? '添加秒杀' : '修改秒杀'"
      :visible.sync="isShow.flag"
      @close="cancel"
    >
      <el-form ref="form" label-width="100px">
        <!-- 通过 menu 组件传过来的 flag 进行 修改  -->

        <el-form-item label="商品名称" prop="name">
          <el-input v-model="form.title"></el-input>
        </el-form-item>

        <el-form-item label="活动期限" prop="name">
          <div class="block">
            <el-date-picker
              v-model="value2"
              type="daterange"
              align="right"
              unlink-panels
              formatter="dateFormat"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :picker-options="pickerOptions"
            >
            </el-date-picker>
          </div>
        </el-form-item>

        <!-- ====================一级================= -->
        <el-form-item label="一级分类">
          <el-select
            v-model="form.first_cateid"
            placeholder="请选择"
            @change="changetwo"
          >
            <el-option label="请选择" :value="0"></el-option>

            <el-option
              v-for="item in CateGetter"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>

        <!-- =================二级================== -->
        <el-form-item label="二级分类">
          <el-select
            v-model="form.second_cateid"
            placeholder="请选择"
            @change="changegoods"
          >
            <!-- 循环便利 一下 下啦选项  -->
            <el-option label="请选择" :value="0" disabled></el-option>

            <el-option
              v-for="item in secondlist"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <!-- ================三级分类================= -->
        <el-form-item label="商品">
          <el-select v-model="form.goodsid" placeholder="请选择">
            <!-- 循环便利 一下 下啦选项  -->
            <el-option label="请选择" :value="0" disabled></el-option>

            <el-option
              v-for="item in threegoods"
              :key="item.id"
              :label="item.goodsname"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="状态" prop="delivery">
          <!-- active-value="1" :inactive-value = "2" -->
          <!-- 设置 什么值打开 什么值关闭  -->
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
          ></el-switch>
        </el-form-item>
      </el-form>
      <!-- =================取消 添加 修改===================== -->

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel()">取 消</el-button>
        <el-button type="primary" v-if="isShow.edit" @click="add()"
          >添 加</el-button
        >

        <el-button type="primary" @click="edit()" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import { mapGetters, mapActions } from "vuex";
import { successAlert, warningAlert } from "../../../util/alert";
import {
  reqseckillAdd,
  reqCateList,
  reqGoodsAdd,
  reqseckillOneEdite,
  reqseckillEdit,
} from "../../../util/request";

export default {
  computed: {
    ...mapGetters({
      SeckillGetter: "seckill/SeckillGetter",
      // 获取一级分类 下拉列表
      CateGetter: "cate/CateGetter",
      // 获取 三级 分类
      goodsGetter: "goods/goodsGetter",
    }),
  },

  props: ["isShow"],

  mounted() {
    // 进入之后就直接进行加载数据
    this.actionGoodsList(true);
    // add 挂载完成之后 进行 加载 一级分类
    this.actionCateList();
  },

  methods: {
    ...mapActions({
      actionSeckillList: "seckill/actionSeckillList",
      actionCateList: "cate/actionCateList",
      actionGoodsList: "goods/actionGoodsList",
    }),

    // 取消函数
    cancel() {
      this.form = {
        first_cateid: 0,
        second_cateid: 0,
        title: "",
        status: 1,
        begintime: "",
        endtime: "",
        goodsid: 0,
      };
      this.threegoods = [];
      

      this.isShow.flag = false;
    },

    // 更改 二级列表
    changetwo() {
      // 更改的时候进行重置
      this.form.second_cateid = "";

      // 这里注意一下 其中 需要给 商品 非类
      reqCateList({ pid: this.form.first_cateid }).then((res) => {
        this.secondlist = res.data.list;
      });
    },

    // 商品函数
    changegoods() {
      // 其中 this.form 中有 second-id
      // this.goodsGetter 中也有 second-id

      this.threegoods = [];

      this.goodsGetter.forEach((item) => {
        if (item.second_cateid == this.form.second_cateid) {
          this.threegoods.push(item);
        }
      });
    },

    // 进行添加函数 发送请求 然后 判断状态码
    add() {
      this.form.begintime = Date.parse(new Date(this.value2[0]));
      this.form.endtime = Date.parse(new Date(this.value2[1]));
      // 进行拆分赋值

      reqseckillAdd(this.form).then((res) => {
        if (res.data.code == 200) {
          // 状态码正确进行 弹窗 清空 以及重新加载
          this.cancel();
          successAlert(res.data.msg);
          this.actionSeckillList();
        } else {
          warningAlert(res.data.msg);
          this.cancel();
        }
      });
    },
    // 进行 获取某一条数据并添加进去

    getOne(id) {
      reqseckillOneEdite(id).then((res) => {
        this.form = res.data.list;

        this.form.id = id;
        let a = new Date(res.data.list.begintime / 1);
        let b = new Date(res.data.list.endtime / 1);
        this.value2 = [a, b];
        
        // 重新请求 一下 渲染 二级列表 
        reqCateList({ pid: this.form.first_cateid }).then((res) => {
          this.secondlist = res.data.list;
        });

        // 重新渲染获取 商品
        this.goodsGetter.forEach((item) => {
          if (item.id == this.form.goodsid) {
            this.threegoods.push(item);
          }
        });

      });
    },

    // 进行修改
    edit() {
      reqseckillEdit(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.actionSeckillList(true);
          this.cancel();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },

  data() {
    return {
      form: {
        first_cateid: 0,
        second_cateid: 0,
        title: "",
        status: 1,
        begintime: "",
        endtime: "",
        goodsid: 0,
      },

      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },

      value2: [],
      // 二级分类的列表
      secondlist: [],

      // 三级分类 商品的 分类
      threegoods: [],

      // 商品属性了列表
      specslist: [],
    };
  },
};
</script>


<style scpoed>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

img {
  width: 178px;
  height: 178px;
}
.el-form-item__content {
  margin-left: 30px;
}
</style>




